import { distanceTo } from '../lib';
import { Core } from './';
export class Common {
    core: Core;
    constructor(core: Core) {
        this.core = core;
    }
    public getCloseSite(x: number, y: number) {
        for (let i = 0; i < this.core.app.project.sites.length; ++i) {
            const site = this.core.app.project.sites[i];
            const dis = distanceTo(site.vec.x, site.vec.y, 0, x, y, 0);
            if (dis < site.r) {
                // console.log(dis, site.r);
                return site;
            }
        }
        return undefined;
    }
    public toScreen(lon: number, lat: number) {
        return this.core.app.renderer.toScreen(lon, lat);
    }
    public toMap(x: number, y: number) {
        return this.core.app.renderer.toMap(x, y);
    }
    get zoom() {
        return this.core.app.renderer.mView.zoom;
    }
    set zoom(v) {
        this.core.app.renderer.mView.zoom = v;
    }
    public center(lon: number, lat: number) {
        return this.core.app.renderer.center(lon, lat);
    }

    public checkHover(x: number, y: number) {
        this.core.app.project.sites.forEach((site) => {
            site.isHover(x, y)
        });
    }
}