import { WaterHyacinth } from '../';
import { RendererJSAPI } from '../../../../../lib'
import { getFalseColor, distanceTo } from 'njscore';

export class Renderer extends RendererJSAPI {
    public app: WaterHyacinth;

    colorArray: number[][] = [];
    colorArray2: number[][] = [];


    constructor(app: WaterHyacinth, view: any) {
        super(view);
        this.app = app;



        this.colorArray = [];
        for (let i = 0; i < 255; ++i) {
            const result = getFalseColor(i / 255);
            this.colorArray.push(result);
        }

        this.colorArray2 = [];

    }
    public init(){
        this.zoom(3);
        this.center(-98.292649, 39.043903)
        this.start();
    }

    render(ctx: CanvasRenderingContext2D) {
        ctx.globalCompositeOperation = 'screen'; //'lighten';

        const xOff = ctx.canvas.width - 300;
        const yOff = ctx.canvas.height - 50;
        const w = 1;
        const h = 10;
        for (let i = 0; i < this.colorArray.length; ++i) {
            ctx.beginPath();
            ctx.rect(i * w + xOff, yOff, w, h)
            ctx.closePath();
            ctx.fillStyle = `rgba(${this.colorArray[i][0]}, ${this.colorArray[i][1]}, ${this.colorArray[i][2]}, 1)`;
            ctx.fill();
        }

        this.app.core.scene.render(ctx);

    }
    keyDown(k: KeyboardEvent): void {
        //     console.log('key pressed', k);
    }
    mouseDown(x: number, y: number): void {
        this.app.core.activeCommand.mouseDown(x, y);
    }
    mouseUp(x: number, y: number): void {
        this.app.core.activeCommand.mouseUp(x, y);
    }
    mouseClick(x: number, y: number): void {
        this.app.core.activeCommand.mouseClick(x, y);
    }
    mouseMove(x: number, y: number) {
        this.app.core.activeCommand.mouseMove(x, y);
    }
 }