import { WaterHyacinth } from '../';
export class UI {
    solution: WaterHyacinth;

    constructor(solution: WaterHyacinth) {
        this.solution = solution;

        let container = document.createElement('div');
        container.style.position = 'absolute';
        container.style.width = '200px';
        container.style.height = '100%';
        container.style.padding = '10px';
        container.style.background = 'rgba(40, 40, 40, 1)';
        this.solution.renderer.hostDiv.append(container);

        let title = document.createElement('div');
        title.textContent = 'map'
        container.appendChild(title);

        let divContainer = document.createElement('div');
        // divContainer.style.position = 'absolute';
        container.appendChild(divContainer);

        let div = document.createElement('div')
        divContainer.appendChild(div);
        div.textContent = 'tracing mode';
        // div.style.position = 'absolute';
        div.onclick = () => {
            // this.solution.renderer.startCapture();
        }

        div = document.createElement('div')
        divContainer.appendChild(div);
        div.textContent = 'log';
        // div.style.position = 'absolute';
        div.onclick = () => {
            // this.solution.renderer.endCapture();
        }
    }
}